.theme-three-main-background {
  background-color: hsl(268, 75%, 9%);
}
.theme-three-keys-background {
  background-color: hsl(268, 71%, 12%);
}
.theme-three-output-colors {
  background-color: hsl(268, 71%, 12%);
  color: hsl(52, 100%, 62%);
}
.theme-three--header-colors {
  color: hsl(52, 100%, 62%);
}
.theme-three-del-btn {
  color: white;
  background-color: hsl(285, 91%, 52%);

  border-bottom: hsl(285, 91%, 52%) 3px solid;
}
.theme-three-numeric-btn {
  background-color: hsl(268, 47%, 21%);
  color: hsl(52, 100%, 62%);
  border-bottom: 3px solid hsl(290, 70%, 36%);
}
.theme-three-equl-btn {
  color: hsl(198, 20%, 13%);
  background-color: hsl(176, 100%, 44%);
  font-size: 1.3rem;
  border-bottom: hsl(177, 92%, 70%) 3px solid;
}
.theme-three-reset-btn {
  color: white;
  background-color: hsl(281, 89%, 26%);
  border-bottom: hsl(285, 91%, 52%) 3px solid;
}

.theme-three-equl-btn:hover {
  background-color:hsl(285, 91%, 52%);
}
.theme-three-del-btn:hover {
  background-color: hsl(285, 91%, 52%);
}

.theme-three-reset-btn:hover {
  background-color: hsl(285, 69%, 65%);
}
