.theme-one-main-background{
  background-color: hsl(222, 26%, 31%);
}
.theme-one-keys-background {
  background-color: hsl(223, 31%, 20%);
}
.theme-one-output-colors {
  background-color: hsl(224, 36%, 15%);
  color: hsl(30, 25%, 89%);
}
.theme-one--header-colors{
    color: hsl(30, 25%, 89%);
}
.theme-one-equl-btn {
  background-color: hsl(6, 63%, 50%);
  font-size: 1.3rem;
  color: hsl(30, 25%, 89%);
  border-bottom: hsl(6, 70%, 34%) 3px solid;
}
.theme-one-numeric-btn {
  background-color: hsl(30, 25%, 89%);
  color: hsl(221, 14%, 31%);
  border-bottom: 3px solid hsl(28, 16%, 65%);
}
.theme-one-del-btn {
  color: hsl(30, 25%, 89%);
  background-color: hsl(225, 21%, 49%);
  border-bottom: hsl(224, 28%, 35%) 3px solid;
}
.theme-one-reset-btn {
  color: hsl(30, 25%, 89%);
  background-color: hsl(225, 21%, 49%);
  border-bottom: hsl(224, 28%, 35%) 3px solid;
}


